import React from "react"
import colorContext from "../../colorContext"
import styles from "./pixel.module.scss"

interface Props {
  className?: string;
  row: number;
  column: number;
}

export default function Pixel({ className, row, column }: Props) {
  const { frontColor, grid, setElement } = React.useContext(colorContext)
  const color = grid[row][column];
  function updateColor(event: React.MouseEvent<HTMLDivElement>) {
    if (event.buttons === 1) {
      // left mouse button pressed
      setElement(row, column, frontColor);
    }
    if (event.buttons === 2) {
      // right mouse button pressed
      setElement(row, column, "#ffffff");
    }
  }
  return (
        <div
          onMouseOver={updateColor}
          onMouseDown={updateColor}
          style={{ backgroundColor: color }}
          onContextMenu={event => event.preventDefault()}
          className={`${className ? className : ""} ${styles.root}`}
        ></div>
  )
}
