import React from "react"
import colorContext from "../../colorContext"
import styles from "./panelColor.module.scss"

interface Props {
  color: string;
  count: number;
  className?: string;
}

export default function PanelColor({ color, count, className }: Props) {
  const { frontColor, setFrontColor } = React.useContext(colorContext)
  return (
    <div
      onClick={() => setFrontColor(color)}
      className={`${className ? className : ""} ${styles.root}`}
      style={{ boxShadow: frontColor === color ? "0 8px 6px -6px black" : "none" }}
    >
      <div className={styles.color} style={{ backgroundColor: color }} />
      <div className={styles.text}>{count}</div>
    </div>
  )
}
