import React from "react"
import colorContext from "../colorContext";
import ColorPanel from "../components/colorPanel/ColorPanel"
import Grid from "../components/grid/Grid"
import Order from "../components/order/Order";
import { generateGrid } from "../util/grid";
import styles from "./index.module.scss"

export default function Home() {
  const [frontColor, setFrontColor] = React.useState("#000000");
  const [grid, setGrid] = React.useState(generateGrid("#ffffff"));
  const setElement = (row, column, value) => {
    const g: string[][] = JSON.parse(JSON.stringify(grid));
    g[row][column] = value;
    setGrid(g);
  }
  return (
    <colorContext.Provider value={{frontColor, setFrontColor, grid, setGrid, setElement}}>
      <div className={styles.root}>
        <Grid className={styles.grid}/>
        <ColorPanel className={styles.panel}/>
        <Order className={styles.order}/>
      </div>
    </colorContext.Provider>
  )
}
