import React from "react"
import colorContext from "../../colorContext"
import PanelColor from "../panelColor/PanelColor"
import styles from "./colorPanel.module.scss"

interface Props {
  className?: string
}

export default function ColorPanel({ className }: Props) {
  const { grid } = React.useContext(colorContext)
  const colors = ["#ffffff", "#cc0000", "#009933", "#0033cc", "#000000"]
  return (
    <div className={`${className ? className : ""}`}>
      {colors.map(c => {
        const count = grid.reduce((counter, row) => {
          return row.reduce((counter2, color) => {
            return color === c ? counter2 + 1 : counter2
          }, counter)
        }, 0)
        return <PanelColor className={styles.element} key={c} color={c} count={count} />
      })}
    </div>
  )
}
